/**
Some REGEX Replacement for old icons
Search:
	content:[\s\t]['"]([\\a-zA-Z0-9]{5})['"];[\s\t]// icon: mdi-([a-z-]+)
Replace:
	content: mdi\("\2"\);\t// icon: mdi-\2
*/

// Blank Material Design Icon replacement
%imageset-glyph {
  position: relative;
  text-indent: 999px;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    font-size: 13px;
    line-height: 13px;
    vertical-align: baseline;
    font-weight: normal;
    font-style: normal;
    text-transform: none;
    text-indent: 0;
    pointer-events: none;
  }
}

%imageset-mdi {
  @extend %imageset-glyph;
  &::after {
    font-family: "Material Design Icons";
  }
}

%imageset-16 {
  width: 16px;
  height: 16px;
  &::after {
    top: 50%;
    bottom: auto;
    margin-top: -8px;
    height: 16px;
    font-size: 14px;
    line-height: 16px;
  }
}

%imageset-12 {
  width: 12px;
  height: 12px;
  &::after {
    top: 50%;
    bottom: auto;
    margin-top: -6px;
    height: 12px;
    font-size: 10px;
    line-height: 12px;
  }
}

%imageset-arrow {
  // For latest topic arrows
  width: 11px;
  height: 10px;
  &::after {
    top: 50%;
    bottom: auto;
    margin-top: -5px;
    height: 10px;
    font-size: 10px;
    line-height: 10px;
  }
}

// Former imageset
span.imageset {
  display: inline-block !important;
  background: transparent none 0 0 no-repeat;
  margin: 0;
  padding: 0;
  width: 0;
  height: 0;
  overflow: hidden;
}

