%icon-image {
  position: relative;
  background-image: none;
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    height: 14px;
    margin-top: -7px; //test
    text-align: center;
    font-size: 12px;
    line-height: 14px;
    vertical-align: baseline;
    font-weight: normal;
    font-style: normal;
    text-transform: none;
    text-indent: 0;
    pointer-events: none;
  }
  &.small-icon::after {
    width: 18px;
    text-align: center;
  }
}

%icon-image-mdi {
  @extend %icon-image;
  &::after {
