.pm_read {
  @extend .topic_read;
}

.pm_unread {
  @extend .topic_unread;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 1.5dppx) {
  .global_read,
  .global_read_locked,
  .global_unread,
  .global_unread_locked,
  .forum_link,
  .forum_read,
  .forum_read_locked,
  .forum_read_subforum,
  .forum_unread,
  .forum_unread_locked,
  .forum_unread_subforum,
  .topic_moved,
  .topic_read,
  .topic_read_mine,
  .topic_read_hot,
  .topic_read_hot_mine,
  .topic_read_locked,
  .topic_read_locked_mine,
  .topic_unread,
  .topic_unread_mine,
  .topic_unread_hot,
  .topic_unread_hot_mine,
  .topic_unread_locked,
