//
// --------------------------------------------------------------
// Colours and backgrounds for cp.css
// --------------------------------------------------------------
// Main CP box
// ----------------------------------------
.panel-container .panel li.header dd,
.panel-container .panel li.header dt {
  color: $bold-text-color;
}

.panel-container table.table1 thead th {
  color: $post-text-color;
  border-bottom-color: $block-inner-border;
}

#cp-main .pm-message {
  border-color: $block-border;
  background-color: $block-background;
}

// CP tabbed menu
// ----------------------------------------
.tabs {
  ul {
    background-color: $tab-background;
  }
  .tab {
    &:not(.responsive-tab) a,
    & a.responsive-tab-link {
      color: $tab-color;
      border-bottom-color: $tab-background;
      &:hover {
        color: $tab-hover-color;
        background: $tab-hover-background;
        border-bottom-color: $tab-hover-border-bottom;
      }
    }
    &.activetab:not(.responsive-tab) a,
    &.activetab a.responsive-tab-link {
      color: $tab-selected-color;
      background: $tab-selected-background;
      border-bottom-color: $tab-selected-border-bottom;
    }
    & a.responsive-tab-link:hover,
    &.activetab a.responsive-tab-link {
      border-bottom-color: $tab-background;
    }
  }
}

// Mini tabbed menu used in MCP
// ----------------------------------------
#minitabs .tab > a {
  background-color: $block-background2;
}

#minitabs .activetab > a,
#minitabs .activetab > a:hover {
  background-color: #F9F9F9;
  color: #333333;
}

// UCP navigation menu
// ----------------------------------------
// Link styles for the sub-section links
#navigation a {
  color: $text-color;
  background: $block-background;
}

#navigation a:hover {
  background: $block-background2;
  color: $link-hover-color;
}

#navigation #active-subsection a {
  background: $primary-color;
  color: $overlay-color;
}

#navigation #active-subsection a:hover {
  background-color: $secondary-color;
}

#navigation > hr {
  border-top-color: $block-background2;
}

// Preferences pane layout
// ----------------------------------------
.panel-container h2 {
  color: $post-text-color;
}

.panel-container .panel {
  background: $block-background;
  box-shadow: none;
  border-width: 0;
  & > .inner > h3:first-child {
    @include section-heading-cp;
  }
}

#cp-main .pm {
  background-color: $block-background;
}

// Friends list
.cp-mini {
  background-color: $block-background2;
}

// PM Styles
// ----------------------------------------
// PM Message history
.current {
  color: $bold-text-color !important;
}

// PM marking colours
.pmlist li.pm_message_reported_colour,
.pm_message_reported_colour {
  border-left-color: #BC2A4D;
  border-right-color: #BC2A4D;
}

.pmlist li.pm_marked_colour,
.pm_marked_colour {
  border-color: #FF6600;
}

.pmlist li.pm_replied_colour,
.pm_replied_colour {
  border-color: #A9B8C2;
}

.pmlist li.pm_friend_colour,
.pm_friend_colour {
  border-color: #5D8FBD;
}

.pmlist li.pm_foe_colour,
.pm_foe_colour {
  border-color: #000000;
}

// Avatar gallery
#gallery label {
  background: $block-background2;
  border-color: $block-inner-border;
}

#gallery label:hover {
  background-color: $block-background3;
}

//
// --------------------------------------------------------------
// Colours and backgrounds for forms.css
// --------------------------------------------------------------
// General form styles
// ----------------------------------------
select {
  border-color: $input-border;
  background-color: $input-bg;
  color: $input-text;
}

label {
  color: $text-color;
}

option.disabled-option {
  color: $muted-color;
}

// Definition list layout for forms
// ----------------------------------------
dd label {
  color: $post-text-color;
}

fieldset.fields1 {
  background-color: transparent;
}

// Hover effects
fieldset dl:hover dt label {
  color: $bold-text-color;
}

fieldset.fields2 dl:hover dt label {
  color: inherit;
}

// Quick-login on index page
// Posting page styles
// ----------------------------------------
#message-box textarea {
  color: $input-text;
}

#message-box textarea.drag-n-drop {
  outline-color: rgba($faint-color, 0.5);
}

#message-box textarea.drag-n-drop-highlight {
  outline-color: rgba($muted-color, 0.5);
}

// Input field styles
// ----------------------------------------
.inputbox,
select {
  background-color: $input-bg;
  border-color: $input-border;
  border-width: 1px;
  color: $input-text;
}

.inputbox {
  @include placeholder {
    color: mix($input-text, $input-bg, 70%);
  }
}

.inputbox:hover,
select:hover {
  background-color: $input-hover-bg;
  border-color: $input-hover-border;
  color: $input-hover-text;
}

.inputbox:focus,
select:focus {
  background-color: $input-focus-bg;
  border-color: $input-focus-border;
  color: $input-focus-text;
}

.inputbox:hover {
  @include placeholder {
    color: $input-hover-text;
  }
}

.inputbox:focus {
  @include placeholder {
    color: transparent;
  }
}

// Form button styles
// ----------------------------------------
a.button1,
input.button1,
input.button3,
a.button2,
input.button2,
button.button2 {
  color: $button-text;
  border-color: $button-border;
  background-color: $button-bg;
}

a.button1,
input.button1 {
  border-color: $primary-color;
  background-color: $primary-color;
  color: $overlay-color;
}

input.button3 {
  background-image: none;
}

// Hover states
a.button1:hover,
input.button1:hover,
a.button2:hover,
input.button2:hover,
button.button2:hover,
input.button3:hover {
  border-color: $secondary-color;
  background-color: $secondary-color;
  color: $overlay-color;
}

// Focus states
input.button1:focus,
input.button2:focus,
button.button2:focus,
input.button3:focus {
  border-color: $secondary-color;
  background-color: $secondary-color;
  color: $overlay-color;
}

input.disabled {
  color: $muted-color;
}

// jQuery popups
// ----------------------------------------
.phpbb_alert {
  background-color: $content-background;
  border-color: $block-inner-border;
}

.phpbb_alert .alert_close::before {
  color: $link-color;
}

.phpbb_alert .alert_close:hover::before {
  color: $link-hover-color;
}

.phpbb_alert .alert_close::after {
  color: $content-background;
}

#darken {
  background-color: #000;
}

#loading_indicator {
  background-color: #000;
  background-image: url("./images/loading.gif");
}

.dropdown-extended ul li {
  border-top-color: $block-inner-border;
}

.dropdown-extended .header,
.dropdown-extended .footer {
  border-color: $block-border;
  border-style: solid;
  color: $dropdown-heading-color;
  a {
    color: $dropdown-heading-link-color;
    &:hover {
      color: $dropdown-heading-link-hover-color;
    }
  }
}

.dropdown-extended .footer {
  background: $dropdown-heading-bottom;
  border-top-width: 1px;
}

.dropdown-extended .header {
  background: $dropdown-heading-top;
  border-bottom-width: 1px;
}

ul.linklist li.responsive-menu a.responsive-menu-link::before {
  color: $link-color;
}

ul.linklist li.responsive-menu a.responsive-menu-link:hover::before,
ul.linklist li.responsive-menu.visible a.responsive-menu-link::before {
  color: $link-hover-color;
}

.dropdown .dropdown-contents {
  background: $dropdown-bg;
  border-color: $dropdown-border;
  /* card effect - dropdown contents */
  @include card($card-default);
  /* card effect end */
}

.dropdown-up .dropdown-contents {
  box-shadow: $dropdown-shadow-up;
}

.dropdown li,
.dropdown li li {
  border-color: $dropdown-inner-border;
}

.dropdown li.separator {
  border-color: $dropdown-inner-border;
}

// Notifications
// ----------------------------------------
.notification_list ul li {
  &.notification-reported strong,
  &.notification-disapproved strong {
    color: $secondary-color;
  } // Normal notification
  &.dropdown-notification {
    background-color: $dropdown-notification-bg;
    color: $dropdown-notification-color;
    a,
    a:hover {
      color: $dropdown-notification-color;
    }
    strong {
      color: $dropdown-notification-strong-color;
    }
    p.notification-time,
    p.notifications_time {
      color: desaturate($dropdown-notification-color, 25%);
    }
  } // Hover normal notification
  &.dropdown-notification:hover:not(.notification-unseen) {
    background-color: $dropdown-notification-hover-bg;
    color: $dropdown-notification-hover-color;
    a,
    a:hover {
      color: $dropdown-notification-hover-color;
    }
    strong {
      color: $dropdown-notification-strong-hover-color;
    }
    p.notification-time,
    p.notifications_time {
      color: desaturate($dropdown-notification-hover-color, 25%);
    }
  } // Unread
  &.notification-unseen {
    background-color: $notification-unread-bg;
    color: $notification-unread-color;
    strong {
      color: $notification-unread-strong-color;
    }
    a,
    a:hover {
      color: $notification-unread-color;
    }
    p.notification-time,
    p.notifications_time {
      color: desaturate($notification-unread-color, 25%);
    }
    a.mark_read {
      /* card effect - notification check button */
      @include card($card-default);
      /* card effect end */
      color: $dropdown-heading-link-color;
      background-color: $dropdown-heading-top;
      &:hover {
        color: $dropdown-heading-link-hover-color;
      }
    }
  }
}


/* COLOUR CLASSES */

.icon-link {
  color: $primary-color;
  &:hover {
    $color: $primary-color;
  }
}

