// Button Styles
// ----------------------------------------
$button-inner-height: $button-line-height + $button-top-padding * 2;
$button-outer-height: $button-inner-height + $button-border-width * 2;
.button {
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  font-family: 'Roboto', sans-serif;
  text-decoration: none !important;
  font-size: $button-font-size;
  padding: $button-top-padding $button-side-padding;
  box-sizing: border-box;
  & > strong {
    font-weight: 600;
  }
  .button-text {
    @media (max-width: 550px) {
      display: none;
    }
  }
}

button.button {
  border-width: 0;
  line-height: normal;
}

.icon-button {
  font-size: 0;
  &::before {
    background: transparent !important;
    font-family: "Material Design Icons";
    content: mdi("wrench"); // icon: mdi-wrench
    display: inline-block;
    font-size: $button-glyph-font-size;
    text-align: center;
    font-weight: normal;
    text-indent: 0;
    font-style: normal;
    text-transform: none;
    .buttons > & {
      position: relative;
      top: -1px;
      right: -1px;
      .rtl & {
        right: auto;
        right: -1px;
      }
    }
  }
}

%icon-button {
  font-family: "Material Design Icons";
}

.dropdown-select.icon-button::before {
  margin-right: 4px;
}

.buttons,
.buttons .button {
  float: left;
}

// Big button images
.reply-icon::before,
.pmreply-icon::before {
  @extend %icon-button;
  content: mdi("reply"); // icon: mdi-reply
}

.post-icon::before,
.newpm-icon::before {
  @extend %icon-button;
  content: mdi("pencil"); // icon: mdi-pencil
}

.reply-all::before {
  @extend %icon-button;
  content: mdi("reply-all"); // icon: mdi-reply-all
}

.locked-icon::before {
  @extend %icon-button;
  content: mdi("pencil-lock"); // icon: mdi-pencil-lock
}

.forwardpm-icon::before {
  @extend %icon-button;
  content: mdi("share"); // icon: mdi-share
}

.modtools-icon {
  width: 22px;
  text-indent: 999px;
  overflow: hidden;
}

.tools-icon::before,
.modtools-icon::before,
.search-icon::before,
.search-adv-icon::before {
  @extend %icon-button;
  @extend %icon-button-visible;
  font-size: 14px;
  height: $button-line-height;
  margin-top: 0;
  width: 16px;
  line-height: $button-line-height;
}

.tools-icon::before {
  content: mdi("wrench"); // icon: mdi-wrench
}

.search-icon::before {
  content: mdi("magnify"); // icon: mdi-magnify
}

.search-adv-icon::before {
  content: mdi("settings"); // icon: mdi-settings
}

.modtools-icon::before {
  content: mdi("gavel"); // icon: mdi-gavel
}

// Icon images
// ----------------------------------------
.small-icon {
  background-position: 0 50%;
  background-repeat: no-repeat;
  background-image: none;
}

.small-icon > a {
  display: inline-block;
  padding: 0 0 0 18px;
}

ul.linklist.bulletin > li.small-icon::before {
  display: none;
}

.dropdown .small-icon > a {
  display: block;
}

// Poster contact icons
// ----------------------------------------
.contact-icons.dropdown-contents {
  min-width: 0;
  padding: 0;
  font-size: 0;
}

.contact-icon {
  position: relative;
  display: block;
  height: 16px;
  width: 16px;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    font-family: "Material Design Icons";
    font-size: 14px;
    line-height: 16px;
    vertical-align: baseline;
    font-weight: normal;
    font-style: normal;
    text-transform: none;
    text-indent: 0;
    pointer-events: none;
  }
}

.contact-icons a {
  border-bottom: 1px dotted;
  border-right: 1px dotted;
  display: block;
  float: left;
  padding: 8px;
}

.contact-icons .last-cell {
  border-right: none;
}

.contact-icons div:last-child a {
  border-bottom: none;
}

.contact-icons div {
  clear: left;
}

