// Post control buttons
// ---------------------------------------------
.post-buttons {
  list-style: none;
  margin-top: 2px;
  .rtl & {
    float: left;
  }
}

.has-profile .post-buttons {
  float: none;
  position: absolute;
  margin: 0;
  top: 5px;
  @if $profile-side==left {
    right: 0;
    .rtl & {
      left: 0;
    }
  }
  @else {
    right: $post-margin;
    .rtl & {
      left: $post-margin;
    }
  }
  .rtl & {
    right: auto;
  }
}

.post-buttons > li {
  float: left;
  margin-right: 3px;
}

// .hastouch .post-buttons .icon-button {
// 	padding: 2px 8px;
// }
.post-buttons .icon-button span {
  display: block;
  height: 0;
  overflow: hidden;
  position: absolute;
  width: 1px;
}

.quote-icon::before {
  @extend %icon-button;
  content: mdi("format-quote-close"); // icon: mdi-format-quote-close
  font-size: 14px;
}

.edit-icon::before {
  @extend %icon-button;
  content: mdi("pencil"); // icon: mdi-pencil
  font-size: 14px;
}

.warn-icon::before {
  @extend %icon-button;
  content: mdi("account-alert"); // icon: mdi-account-alert
  font-size: 14px;
}

.delete-icon::before {
  @extend %icon-button;
  content: mdi("close"); // icon: mdi-close
  font-size: 14px;
}

.report-icon::before {
  @extend %icon-button;
  content: mdi("alert"); // icon: mdi-alert
  font-size: 14px;
}

.info-icon::before {
  @extend %icon-button;
  content: mdi("information"); // icon: mdi-information
  font-size: 14px;
}

.button.responsive-menu-link::before {
  @extend %icon-button;
  content: mdi("menu"); // icon: mdi-menu
  font-size: 14px;
}

.post-buttons > #expand > a {
  @extend .button;
  @extend .icon-button;
  width: 12px;
  overflow: hidden;
  text-indent: 999px;
  &::before {
    @extend %icon-button;
    content: mdi("chevron-down"); // icon: mdi-chevron-down
  }
  &.expanded::before {
    content: mdi("chevron-up"); // icon: mdi-chevron-up
  }
}

// Responsive buttons in post body
.post-buttons .dropdown {
  top: 20px;
}

$post-dropdown-glyphs-font-size: floor($dropdown-content-font-size / 2) * 2;
.post-buttons .dropdown a {
  display: block;
  font-size: $dropdown-content-font-size;
  line-height: $dropdown-content-line-height;
  text-align: left;
  margin-left: 18px;
  position: relative;
  &::before {
    position: absolute;
    left: -16px;
    top: 50%;
    height: $post-dropdown-glyphs-font-size;
    line-height: $post-dropdown-glyphs-font-size + 2px;
    margin: -#{$post-dropdown-glyphs-font-size / 2} 0 0;
    pointer-events: none;
    color: inherit;
  }
  .rtl & {
    text-align: right;
  }
}

.hasjs .postbody .post-buttons {
  max-width: 40%;
}

// Browser-specific tweaks
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
