/* FIXES */


/* ---fix for icon displaying on the user's name */

.navbar.secondary > ul > li > a.small-icon {
  padding-left: 1.5em !important;
}


/* --- spoiler  la wykop.pl */

.spoiler {
  font-size: 14px;
}

a.spoiler {
  font-variant: small-caps;
}

a.spoiler:hover {
  text-decoration: none;
}

div.spoiler {
  font-family: monospace;
  font-size: 14px;
  display: none;
}


/* --- flex bbcode */

div.bb-flex {
  display: flex;
  flex-flow: row wrap;
  align-items: stretch;
  justify-content: flex-start;
  & div.bb-flex-item {
    margin-right: auto;
    display: inline-block;
  }
  div.forum-info & {
    padding: 0 1.3em;
  }
}


/* --- youtube bbcode */

.videoWrapper {
  position: relative;
  padding-bottom: 12.25%;
  padding-top: 25px;
  height: 0;
  max-width: 440px;
  min-height: 150px;
  display: block;
}

.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}


/* utility classes */

.btn-separator {
  margin-left: 1.5em;
  display: inline;
}


/**
 * CARD EFFECT PRESET
 * TODO: When attr implementend do -> opacity = attr(data-card-opacity)
 */

.card[data-card] {
  position: relative;
  /* Primary navbar nav tabs card effect with customizable opacity */
  &::after {
    @include card($card-default);
    opacity: 1; // Block covering all #page-header-box content
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    pointer-events: none;
  }
}

.card[data-card="0"]::after {
  opacity: 0;
}

.imageset.icon_user_online {
  background-image: url("images/icon_user_online.gif");
  padding-left: 58px;
  padding-top: 58px;
}
