/* --------------------------------------------------------------
	$Icons
-------------------------------------------------------------- */


/* Global module setup
--------------------------------*/


/* Renamed version of .fa class for agnostic useage of icon fonts.
 * Just change the name of the font after the 14/1 to the name of
 * the font you wish to use.
 */

.icon,
.button .icon {
  display: inline-block;
  font: normal normal normal 24px/1 "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(0, 0);
}

.icon::before {
  padding: 0 2px;
}

.button:not(.button-icon-only) .icon:first-child::before {
  padding-left: 0;
}

.button:not(.button-icon-only) .icon:last-child::before {
  padding-right: 0;
}


/* Icon size classes - Default size is 14px, use these for small variations */

.icon.icon-xl {
  font-size: 20px;
}

.icon.icon-lg {
  font-size: 16px;
}

.icon.icon-md {
  font-size: 10px;
}

.icon.icon-sm {
  font-size: 8px;
}


/* icon modifiers */

.icon-tiny {
  width: 12px;
  transform: scale(0.65, 0.75);
  vertical-align: text-bottom;
  font-size: 16px;
}

.arrow-right .icon {
  float: right;
}

.post-buttons .dropdown-contents .icon {
  margin-right: 0;
}

.alert_close .icon::before {
  padding: 0;
  border-radius: 50%;
  width: 11px;
  display: block;
  line-height: .9;
  height: 12px;
}
